@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
.header-container {
  display: flex;
  justify-content: center;
  margin-block: 5em;
}


.title {
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);
  border: 2px solid rgb(166, 99, 204);
  border-radius: 2em;
  box-shadow: 2px 2px 5px rgb(28, 2, 33);
  color: rgb(219, 219, 219);
  font-size: 10vw;
  padding: 0 0.3em;
  text-align: center;
}

@media only screen and ( min-width : 1200px ) {

  .title {
    font-size: 120px;
  }
}

@media only screen and ( max-width : 560px ) {

  .title {
    font-size: 48px;
  }
}

.container-title2 {
  display: flex;
  justify-content: center;
  margin-block: 40px;
}

.title2 {
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);
  border: 2px solid rgb(166, 99, 204);
  border-radius: 2em;

  /* border-radius: 100px 0; */
  box-shadow: 2px 2px 5px rgb(28, 2, 33);
  color: rgb(210, 217, 219);

  /* padding: 5px 20px; */
  padding: 0 0.5em;

}

.container-card {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em 1em;
}

.planet-name {
  color: rgb(210, 217, 219);
  font-size: 20px;

  /* opacity: 0.7; */
  padding: 0.2em 0;
  text-align: center;
  text-shadow: 2px 2px 5px rgb(28, 2, 33);
}

.planet-img {
  width: 10vw;
}

@media only screen and ( max-width : 560px ) {

  .planet-img {
    min-width: 50px;
    width: 10vw;
  }
}


.container-planetas {
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);
  border: 2px solid rgb(166, 99, 204);
  border-radius: 20px;
  box-shadow: 2px 2px 5px rgb(28, 2, 33);
  margin-inline: 60px;
}

.first-planets {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.second-planets {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.container-mission {
  align-items: center;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 0 0 10px 10px;
  border-top: 2px solid rgb(166, 99, 204);
  box-shadow: 2px 2px 5px rgb(28, 2, 33);
  color: rgb(210, 217, 219);
  display: flex;
  flex-direction: column;
  margin: 10px;
  padding: 0 20px 20px;
  text-shadow: 2px 2px 5px rgb(28, 2, 33);
  width: 300px;
}

.mission-title {
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  display: flex;
  font-size: 20px;
  height: 80px;
  justify-content: center;
  margin-bottom: 20px;
  text-align: center;
  width: 100%;
}

.mission-description {
  padding-block: 5px;
}

.container-missoes {
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.3);
  border: 2px solid rgb(166, 99, 204);
  border-radius: 20px;
  box-shadow: 2px 2px 5px rgb(28, 2, 33);
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-inline: 60px;
  padding-block: 1em;
}

.bg-img {
  -webkit-animation: rotation 400s infinite linear;
          animation: rotation 400s infinite linear;
  left: -100px;
  opacity: 1;
  position: absolute;
  top: -500px;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  z-index: -1;
}

@-webkit-keyframes rotation {

  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

@keyframes rotation {

  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(-360deg);
            transform: rotate(-360deg);
  }
}

* {
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  overflow-x: hidden;
}

body {
  background-color: rgb(8, 2, 32);
  color: rgb(28, 2, 33);
  font-family: Poppins , sans-serif;
  min-width: 560px;
  overflow-x: hidden;
  position: relative;
}


